
var data = [{
  value: 50,
  name: '报废',
}, {
  value: 25,
  name: '让步接收',
}, {
  value: 25,
name: '返工',
}, ];




var outColor = [
   '#6d5685', '#476ab4','#173471',
]
function _monthErrorOption() {
  const option = {
    title: {
      show:false,
      text: 'Referer of a Website',
      subtext: 'Fake Data',
      left: 'center'
    },
    grid:{
      bottom:20
    },
    legend: {
      data: ["报废",'让步接收','返工' ],
      // 定位到底部
      bottom: 0,
      textStyle: {
        color: "#8cb9d5", // 设置图例文字颜色为红色
      },
    },
    tooltip: {
        show: true,
        trigger: 'item',
        formatter: "{b}: {c} ({d}%)"
    },
    
    series: [{
      name:'pip1',
      radius: ['40%', '70%'],
      emphasis:{
        disabled:true,
      },
        type: 'pie',
        
        selectedMode: 'single',
        color: ['#3370ff','#82a7fc','#bacefd' ],
        
        label: {
          normal: {
              formatter: '{b}:{c}%',
              textStyle: {
                  color: "#8cb9d5",
                  fontWeight: 'bold',
                  fontSize: 14
              }
          }
      },
        labelLine: {
            normal: {
                show: true
            }
        },
        data: data
    }, ],
  };
  return option;
};

const monthErrorOption = _monthErrorOption();

export default monthErrorOption;