

function _weekProductionOption() {
  const option = {
    legend: {
      data: ["不良数量", ],
      textStyle: {
        color: "#8cb9d5", // 设置图例文字颜色为红色
      },
      // 定位到底部
      bottom: 0,
    },
    grid:{
      bottom:50
    },
    xAxis: {
      type: "category",
      data: ["分切", "倒角", "其它",],
      axisLabel: {
        textStyle: { color: "#8cb9d5" }, //字体颜色
      },

      axisLine: {
        show: true,
        lineStyle: {
          type: "solid",
          color: "#3B526C", //下边线的颜色
          width: "1", //坐标线的宽度
        },
      },
      splitLine: {
        show: false, //垂直线
        lineStyle: {
          color: "#8cb9d5",
        },
      },
    },
    yAxis: [{
      type: "value",
      axisTick: { show: false }, //尺寸边线
      splitLine: { show: true,
        lineStyle: {
          color: "#fff",
        },
      
      },
      axisLabel: {
        textStyle: { color: "#8cb9d5" }, //字体颜色
      },
      axisLine: {
        show: true,
        lineStyle: {
          type: "solid",
      color: "#fff", //下边线的颜色
          width: "1", //坐标线的宽度
        },
      },
    },
    
  ],
    tooltip: {
      trigger: "axis",
    },
    series: [
      {
        barWidth: "100px",
        label: {
          show: true,
          position: 'top',
          // 字体颜色
          color: '#8cb9d5'
        },
        name: "不良数量",
        type: "bar",
        data: [
          18,
          {
            value:23,
            itemStyle: {
              color: '#8cb9d5'
            }
          },
        20,
        ],
        barGap: "-100%",
        color:['#3a81ff',]
      },
      
    ],
  };
  return option;
};

const weekProductionOption = _weekProductionOption();

export default weekProductionOption;