<template>
  <div class="chart">
    <!-- 头部 -->
    <div
      style="display: flex; justify-content: space-between; align-items: center"
      class="bg-header"
    >
      <i
        title="返回"
        class="ri-corner-up-left-line"
        @click="back"
        style="
          color: #fff;
          cursor: pointer;
          font-size: 16px;
          font-weight: bold;
          flex: 1;
          text-align: center;
        "
      ></i>
      <span
        class="big-title"
        style="
          line-height: 1;
          color: #71d0ec;
          font-weight: bold;
          flex: 1;
          text-align: center;
        "
        >金特工厂-质量监控</span
      >
      <div
        style="
          flex: 1;
          display: flex;
          align-items: center;
          justify-content: center;
          line-height: 1;
        "
      >
        <span
          class="dateTime"
          style="color: #fff; font-weight: bold; margin-right: 20px"
        >
          {{ date }}
        </span>
        <i
          title="全屏"
          class="ri-fullscreen-fill"
          @click="toScreenfull"
          :style="{
            marginLeft: '20px',
            color: '#fff',
            fontSize: '19px',
            cursor: 'pointer',
          }"
        ></i>
      </div>
    </div>

    <!-- 工单数量 -->
    <div style="display: flex">
      <div class="order">
        <div class="order-box">
          <!-- form -->
          <div style="width: 100%">
            <a-form :form="form" layout="vertical">
              <div style="margin-bottom: 20px">
                <p style="margin-bottom: 10px; text-align: left">
                  <span style="color: #fff; margin-right: 10px">质检时间</span>
                  <a-dropdown>
                    <a
                      style="color: #4caf50"
                      @click="(e) => e.preventDefault()"
                    >
                      大于等于 <a-icon type="down" />
                    </a>
                    <a-menu slot="overlay">
                      <a-menu-item>
                        <a href="javascript:;">等于任何一个</a>
                      </a-menu-item>
                    </a-menu>
                  </a-dropdown>
                </p>
                <a-date-picker style="width: 100%;" @change="onChange" placeholder="选择日期" />
              </div>
              <div>
                <p style="margin-bottom: 10px; text-align: left">
                  <span style="color: #fff; margin-right: 10px"
                    >生产工单号</span
                  >
                  <a-dropdown>
                    <a
                      style="color: #4caf50"
                      @click="(e) => e.preventDefault()"
                    >
                      等于任何一个 <a-icon type="down" />
                    </a>
                    <a-menu slot="overlay">
                      <a-menu-item>
                        <a href="javascript:;">等于任何一个</a>
                      </a-menu-item>
                    </a-menu>
                  </a-dropdown>
                </p>

                <a-select
                  v-model:value="form.orderNum"
                  show-search
                  placeholder="输入即可搜索"
                  :show-arrow="false"
                  :filter-option="false"
                  @search="onSearch"
                  @change="handleChange"
                ></a-select>
              </div>
            </a-form>
          </div>
        </div>
        <div class="order-box">
          <span>周报工不良数量</span>
          <span>9</span>
        </div>
        <div class="order-box">
          <span>日报工不良数量</span>
          <span>1</span>
        </div>
        <div class="order-box">
          <span>月报工不良数量</span>
          <span>20</span>
        </div>
      </div>
      <!-- 工序质检处理结果柱状图 -->
      <div style="flex: 1">
        <p style="color: #fff">工序质检处理结果柱状图</p>
        <BaseChart width="1000" height="400" :option="monthErrorOption" />
      </div>
    </div>
    <div style="display: flex; flex-wrap: wrap; flex: 1; overflow: hidden">
      <div class="chart-one-item table-box">
        <p style="color: #fff">质检工序不良统计</p>
        <BaseChart width="100%" height="470" :option="weekProductionOption" />
      </div>

      <div class="chart-one-item table-box">
        <div class="wrap-table">
          <a-table
            id="tables"
            :title="
              () => {
                return '工序质检明细';
              }
            "
            :bordered="true"
            :columns="columns"
            :data-source="dataList"
            size="small"
            :scroll="{ x: '100%', y: tableHeight }"
            :rowClassName="rowClassName"
            :pagination="{
              pageSize: 20,
              total: dataList.length,
            }"
          >
          </a-table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// import screenfull from "screenfull";
import { getTableScroll } from "@/utils/getTableScroll";
import weekProductionOption from "./week-production";
import monthErrorOption from "./month-error";
export default {
  components: {},
  data() {
    return {
      isScreenfull: false,
      date: "",
      tableHeight: 100,
      weekProductionOption,
      monthErrorOption,
      form: {
        orderNum: undefined,
      },

      columns: [
        {
          title: "选择工单",
          dataIndex: "workOrder",
          key: "workOrder",
        },
        {
          title: "选择工序",
          dataIndex: "process",
          key: "process",
        },
        {
          title: "检验类型",
          dataIndex: "inspectionType",
          key: "inspectionType",
        },
        {
          title: "检验数量",
          dataIndex: "inspectionQuantity",
          key: "inspectionQuantity",
        },
        {
          title: "不良数量",
          dataIndex: "defectQuantity",
          key: "defectQuantity",
        },
      ],
      dataList: [
        {
          workOrder: "WO001",
          process: "工序A",
          inspectionType: "类型A",
          inspectionQuantity: 100,
          defectQuantity: 5,
        },
      ],
    };
  },

  mounted() {
    const currentDate = new Date();
    const year = currentDate.getFullYear();
    const month = String(currentDate.getMonth() + 1).padStart(2, "0");
    const day = String(currentDate.getDate()).padStart(2, "0");

    this.date = `${year}-${month}-${day}`;

    this.$nextTick(() => {
      this.tableHeight = getTableScroll({ id: "tables" });
    });
  },
  beforeDestroy() {},
  methods: {
    // 生成数据
    genData() {
      return this.type.map((item) => Math.ceil(Math.random() * 1000));
    },
    back() {
      if (this.isScreenfull) {
        this.toScreenfull();
      }
      this.$router.go(-1);
    },
    toScreenfull() {
      this.isScreenfull = !this.isScreenfull;
    //   screenfull.toggle();
    },
    // 搜索
    onSearch() {},
    handleChange() {},
    rowClassName: (record, index) => {
      let rowClassName = "black";

      if (index % 2 === 0) {
        rowClassName = "blue";
      }

      return rowClassName;
    },
    // 日期选择
    onChange() {},
  },
};
</script>

<style lang="scss" scoped>
.chart {
  background: #03090f;
  display: flex;
  flex-direction: column;
  padding: 0 vw(45) 0;
  box-sizing: border-box;
  height: 100vh;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  .table-box {
    margin-top: vh(20);
    margin-right: vw(30);
    margin-bottom: vh(30);
    border-radius: 4px;
    &:first-child {
      width: vw(700);
    }
    &:last-child {
      flex: 1;
      margin-right: 0;
      border: 1px solid #0f80dd;
    }
    .wrap-table {
      height: 100%;
    }
  }
}

:deep(.ant-table-bordered .ant-table-thead > tr > th) {
  border-bottom: 0.5px solid #0f80dd;
  border-right: 0.5px solid #0f80dd;
}

:deep(.ant-table-tbody > tr > td) {
  border-bottom: 0.5px solid #0f80dd;
  border-right: 0.5px solid #0f80dd;
}

h4 {
  color: #71d0ec;
  text-align: center;
  line-height: 1;
  font-weight: bold;
  margin-bottom: 12px;
  font-size: fs(20);
}

::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 0;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-button {
  display: none;
}
::-webkit-scrollbar-corner {
  display: none;
}

::-webkit-scrollbar-thumb {
  cursor: pointer;
  border-radius: 5px;
  background: rgba(177, 177, 177, 0.15);
  transition: color 0.2s ease;
}

.dateTime {
  font-size: fs(20);
}

.big-title {
  font-size: fs(24);
}

.bg-header {
  margin-top: vh(20);
  margin-bottom: vh(35);
}

.order {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: vw(30);
  width: vw(700);
  flex-wrap: wrap;
  .order-box {
    height: vh(200);
    width: 48%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background-image: url("@/assets/ksh39.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    margin-bottom: vh(20);
    &:first-child {
      background-image: none;
    }
    & > span {
      font-size: fs(34);
      color: #fff;
    }
  }
}

:deep(.ant-table) {
  color: #fff;
  .ant-table-title {
    background-color: #0b1e3c;
    border: none;
  }
  .ant-table-thead {
    tr {
      background-color: #326ffd;
      th {
        color: #fff;
      }
    }
  }
}
:deep(.ant-table-small > .ant-table-content .ant-table-header) {
  background-color: #326ffd;
}

:deep(.ant-table-small) {
  border: none;
}

:deep(.black) {
  background-color: #010a13 !important;
  td {
    background-color: #010a13 !important;
  }
}
:deep(.blue) {
  background-color: #0d1d3f !important;
  td {
    background-color: #0d1d3f !important;
  }
}

:deep(.ant-table-body) {
  // &::-webkit-scrollbar-track {
  // background: transparent;
  // border-radius: 0;
  // }

  &::-webkit-scrollbar {
    // -webkit-appearance: none;
    width: 6px;
    height: 6px;
    background: #010a13;
  }

  &::-webkit-scrollbar-button {
    display: none;
    background: #010a13;
  }
  &::-webkit-scrollbar-corner {
    display: none;
    background: #010a13;
  }

  &::-webkit-scrollbar-thumb {
    cursor: pointer;
    border-radius: 5px;
    background: #666c73;
  }

  &::-webkit-scrollbar-track {
    /*滚动条里面轨道*/
    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    background: #010a13;
  }
}

:deep(.ant-select-selection) {
  background-color: #010a13;
}
:deep(.ant-input) {
  background-color: #010a13;
}

:deep(.ant-form-item-label label) {
  color: #fff;
}
</style>
